.footer-disclaimer {
  display: grid;
  grid-area: contributor;
  font-weight: 525;
  opacity: 0.8;
  color: #941414;
}

.footer-product {
  display: grid;
  grid-area: product;
  font-weight: 525;
  opacity: 0.8;
}

.footer-support {
  display: grid;
  grid-area: community;
  font-weight: 525;
  opacity: 0.8;
}

.contributor-link,
.product-link,
.support-link {
  text-decoration: none;
  color: white;
  padding-top: 8px;
  font-weight: 400;
  font-size: 14px;
}

.footer__bottom-container {
  display: flex;
  justify-content: space-evenly;
