#Root {
  height: 100vh;
  width: 100vw;
}

html {
  font-family: 'Maison Neue Book', 'Helvetica Neue', Helvetica, Arial,
    'Liberation Sans', FreeSans, sans-serif;
  font-size: 62.5%;
  font-weight: 400;
  line-height: 1;
  height: 100vh;
  overflow: hidden;
  width: 100vw;
}

body {
  background: #36393f;
  height: 100vh;
  margin: 0;
  font-size: 1.4rem;
  line-height: 1.5;
  color: #282828;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.sign-up-container {
  height: calc(100vh - 21.5vh);
  width: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.sign-up-wrapper {
  background-color: #202225;
  padding: 40px;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  z-index: 1;
  margin-right: 730px;
}

.form-title {
  font-family: 'Open Sans', sans-serif;
  font-size: 50px;
  color: white;
  font-weight: 800;
  text-align: center;
}

.form-sub-title {
  font-family: 'Open Sans', sans-serif;
  font-size: 30px;
  color: white;
  font-weight: 300;
  text-align: center;
}

.text-wrapper {
  height: 100px;
  display: flex;
  flex-direction: column;
  margin-bottom: 50px;
}

.form-div {
}

.label {
  color: white;
  margin-bottom: 10px;
}
.label-wrapper {
  display: flex;
  flex-direction: column;
}

.input {
  font-family: 'Open Sans', sans-serif;
  margin-bottom: 10px;
  border-bottom: 3px solid #1fbea9;
  border-top: none;
  border-left: none;
  border-right: none;
  background-color: #202225 !important;
  color: white;
  font-weight: 800;
}

.input:focus {
  border-bottom: 3px solid #257ae9;
  color: white;
  font-family: 'Open Sans', sans-serif;
  font-weight: 800;
}

.input:active {
  border-bottom: 3px solid #25e9cf;
  color: white;
  font-family: 'Open Sans', sans-serif;
  font-weight: 800;
}

.form-image {
  bottom: 11%;
  right: -10%;
  height: auto;
  width: 1400px;
  z-index: 0;
  position: absolute;
}

.login-container {
  height: calc(100vh - 21.5vh);
  width: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.button-wrapper {
  display: flex;
  width: 100%;
}

.cta {
  font-family: 'Open Sans', sans-serif;
  color: white;
  font-size: 10px;
}
.cta:hover {
  color: #257ae9;
}

.cta-wrapper {
  margin-top: 20px;
}

.cta-1 {
  font-family: 'Open Sans', sans-serif;
  color: white;
  font-size: 10px;
}

.input-email,
.input-password {
  font-family: 'Open Sans', sans-serif;
  margin-bottom: 10px;
  border-bottom: 3px solid #1fbea9;
  border-top: none;
  border-left: none;
  border-right: none;
  background-color: #202225 !important;
  color: white;
  font-weight: 800;
}

.input-email:focus,
.input-password:focus {
  border-bottom: 3px solid #257ae9;
  color: white;
  font-family: 'Open Sans', sans-serif;
  font-weight: 800;
}

.input-email:active,
.input-password:active {
  border-bottom: 3px solid #25e9cf;
  color: white;
  font-family: 'Open Sans', sans-serif;
  font-weight: 800;
}

.navbar-wrapper {
