a {
  text-decoration: none !important;
}

.logo-text {
  color: white;
  font-weight: 800;
  font-family: 'Open Sans', sans-serif;
}

.balance-wrapper {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  color: white;
  background-color: #36393f !important;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
