Body {
  width: 100%;
}

.home-wrapper {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  height: calc(100vh - 21.5vh);
}

.home-content-wrapper {
  display: flex;
  width: 62%;
}

.heading {
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  color: white;
