  justify-content: space-evenly;
}

.card {
  height: 150px;
  background-color: #7289da;
  width: 250px;
  margin-top: 20px;
  border-radius: 5px;
  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.card:hover {
